	module.exports = {"data":{"markdownRemark":{"html":"<p>有人告诉我：\"你的一名学生(现在已经是老师)对研究生说，王沪宁的书我一本也没有看过，听说《美国反对美国》写得最好(意思也不想去看)。学生那边反应很大。\"云云。</p>\n<p>我想是不是有这么一回事，有了又怎么样?说这样话的人，最终是会被别人笑话的。打倒名人成名人，在这个世界上是通行的逻辑。有些人是\"狂\"，自己没有写过书，别人的书都看不上眼，我看做不出名堂，首先是不能兼收并蓄，博览群书。有些人是抬高自己，不是一个有修养的人的表现，别人在后面议论他的，首先是不知道中国人如何做学问，中国学问在于如何做人。</p>\n<p>听到此番话，有一瞬间的恼火，说明修养也不够。学问之事，全在贡献于社会，清淡出自身，并不在乎别人如何说。</p>\n<p>但是，如果是自己的学生，则说明教之失严了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0174.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0174.md","frontmatter":{"title":"11月1日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0174.md absPath of file >>> MarkdownRemark"}}

